<template>
	<div class="cy_page3">
		<div class="cont box-border-shadow">
			<div v-show="showGraph" style="position: relative;">
				<div id="graphCharts" style="width: 100%;height: 800px;"></div>
			</div>
			<div v-show="!showGraph" style="width: 100%;height: 800px;">
				<img style="display:block;margin: 100px auto;" src="../../../assets/no-sj.png">
			</div>
		</div>
	</div>
</template>
<script type="text/javascript">
	import * as echarts from 'echarts'
	import reqData from "./../../../reqData.js"
	export default{
		name:'cw_yw',
		data(){
			return {
				showGraph:true,
			}
		},
		components:{
		},
		computed:{
			company(){
				return JSON.parse(sessionStorage.getItem("company"));
			},
		},
		methods:{
		  unique(arr){
			var newArr = [arr[0].name];
	   		var data=[arr[0]];
	   		//去重复项
	  　　 	for(var i=1;i<arr.length;i++){
	　　　　		if(newArr.indexOf(arr[i].name) == -1){
	          　　  newArr.push(arr[i].name);
	          	    data.push(arr[i]);
	    　　  	}
    		}
    		//区分值和主题
    		console.log(data.length)
    		for(var i=0;i<data.length;i++){
    		 	if(data[i].tag&&data[i].tag==='业务'){
    		 		data[i].itemStyle={
			             normal: {
			                 color: '#68bdf6',
			                 borderColor:'#5ca8db',
			                 borderWidth:2,
			             }
			         }
			         data[i].symbol='diamond'
    		 	}
    		}
    		console.log(data);
	        return data;
		  },
		  getData(){
		  	 this.showGraph=true;
		 	 var graphCharts = echarts.init(document.getElementById('graphCharts'));
		 	 graphCharts.showLoading();
		 	 reqData
		        .req({
		          apiName: "shenji_caiwu_yewu_zhishitupu_nanshen",
		           data: {             
		             biaoshi:3,
		           }
		        })
		        .then(res => {
		          res = res.data;
		          if (res && res.code === "200") {
		            let data = res.data;
		            console.log(data);
		            let nodes=data.nodes
					let links=data.links;
					nodes=this.unique(nodes);
					graphCharts.hideLoading();
					if(nodes.length>0){
						graphCharts.setOption(this.graphOption(nodes,links)); 
			    	    $(window).resize(function(){
							graphCharts.resize({
								width:'auto',
								height:'auto'
							})
					    })
					}else{
						this.showGraph=false
					}
			    	
		          }
		        })
		        .catch(err => {
		          console.log(err);
		          this.showGraph=false
		       });
		  },
		  graphOption(nodes,links){
		    	let option = {
				     title: {
				         
				     },
				     tooltip: {
				         formatter: function(x) {
				         	let result;
				         	if(x.data.name){
				             	result=x.data.name;
				         	}else{
								let name=x.data.value;	
								name=name.replace(/;/g,";<br/>")
				         		result=x.data.source+" → "+x.data.target+"<br/>"+name;
				         	};
				         	return result;
				         }
				     },
				     legend: {
				        x: "center",
				        show: false,
				        data: ["财务指标", "业务指标"]
				     },
				     series: [{
				        type: 'graph',
				        layout: 'force',
			            categories: [{
			                name: '财务指标',
			                itemStyle:{
			                	borderColor: "#5ca8db",
								borderWidth: 2,
								color: "#68bdf6"
			                },
							symbol: "diamond"
			            }, {
			                name: '业务指标',
			                itemStyle: {
			                    normal: {
					                color: '#ff756e',
					                borderColor:'#e06760',
					                borderWidth:2,
					            },
			                }
			            }],
				         focusNodeAdjacency: true,
				         symbolSize: 68,
				         roam: true,
				         edgeSymbol: ['', 'arrow'],
				         // edgeSymbolSize: [80, 10],
				         hoverAnimation:true,
				         force: {
				         	initLayout:'circular',
				             repulsion: 1020,
            				 // layoutAnimation:false,
            				 gravity:0.5,
				             edgeLength: [86, 180],
				             layoutAnimation:true
				         },
				         draggable: true,
						 // symbol:'diamond',
				         itemStyle: {
				             normal: {
				                 color: '#ff756e',
				                 borderColor:'#e06760',
				                 borderWidth:2,
				             },

				         },
				         lineStyle: {
				             normal: {
				                 width: 1,
				                 color: '#000'

				             }
				         },
				         edgeLabel: {
				             normal: {
				                 show: true,
				                 textStyle: {fontSize: 14,color:'#000'},
				                 formatter: function(x) {
				                 	let name=x.data.value;
						         	if(name.length>5){
										name=name.substr(0,5)+'...';
										// v.nameSub=name;
									}else{
										name=name.substr(0,5);
										// v.nameSub=name;
									}
					                return name;
					            }
				             }
				         },
				         label: {
				         	show:true,
				            formatter: function(x) {
			                 	let name=x.data.name;
					         	if(name.length>5){
									name=name.substr(0,5)+'...';
									// v.nameSub=name;
								}else{
									name=name.substr(0);
									// v.nameSub=name;
								}
								
			                     return '{a|'+name+'}';
			                },
			                rich: {
						        a: {
						            color: '#fff',
						        },
						    }
				         },
				        nodes: nodes, 
						links:links 
				    }]
				};
				return option;
		  }
		},
	    mounted(){
	    	this.getData();
	    }
	}
</script>
<style scoped="">
	.cont{
		width: 100%;
		padding:40px;
		box-sizing: border-box;
	} 
</style>



